import React, { useEffect, useState } from 'react';
import { useTable, useSortBy, usePagination } from 'react-table';

import TableCell from './Cell/Cell';
import EditButton from '../Buttons/EditLink.Button';

const TableReact = ({ columns, data }) => {
	const {
		getTableProps,
		getTableBodyProps,
		headerGroups,
		// rows,
		prepareRow,

		// Pagination
		page,
		canPreviousPage,
		canNextPage,
		pageCount,
		gotoPage,
		nextPage,
		previousPage,
		// Get the state from the instance
		state: { pageIndex },
	} = useTable(
		{
			columns,
			data,
			manualPagination: false,

			// Pagination
			initialState: { pageIndex: 0 },
		},
		usePagination
	);

	// Render the UI for your table
	return (
		<>
			<table {...getTableProps()} className='table align-items-center mb-0'>
				<thead>
					{headerGroups.map((headerGroup) => (
						<tr {...headerGroup.getHeaderGroupProps()}>
							{headerGroup.headers.map((column) => (
								<td
									{...column.getHeaderProps()}
									className='text-uppercase text-secondary text-xxs font-weight-bolder opacity-7 p-2 py-3'>
									{column.render('Header')}
								</td>
							))}
							<td className='text-secondary opacity-7'></td>
						</tr>
					))}
				</thead>
				<tbody {...getTableBodyProps()}>
					{page.map((row) => {
						prepareRow(row);
						return (
							<tr {...row.getRowProps()}>
								<React.Fragment>
									{row.cells.map(TableCell)}
									<EditButton row={row} />
								</React.Fragment>
							</tr>
						);
					})}
				</tbody>
			</table>
			<div className='py-4 card-footer'>
				<nav aria-label='...'>
					<nav className='pagination justify-content-end mb-0' aria-label='pagination'>
						<ul className='justify-content-end mb-0 pagination'>
							<li className={`${canPreviousPage ? '' : 'disabled'} page-item`}>
								<a
									href='#'
									tabindex='-1'
									className='page-link'
									onClick={(e) => {
										e.preventDefault();

										previousPage();
									}}>
									<i className='fas fa-angle-left'></i>
									<span className='sr-only'>Previous</span>
								</a>
							</li>

							{[0, 1, 2, 3, 4].map((pageNoi) => (
								<li className={`page-item ${pageNoi === pageIndex ? 'active' : ''}`} key={pageNoi}>
									<a
										href='#'
										className='page-link'
										onClick={(e) => {
											e.preventDefault();
											gotoPage(pageNoi);
										}}>
										{pageNoi + 1}
									</a>
								</li>
							))}

							<p className='px-2 w-20'>
								<input
									type='number'
									value={pageIndex + 1}
									className='form-control'
									onChange={(e) => {
										gotoPage(e.target.value - 1);
									}}
								/>
							</p>

							{[pageCount - 4, pageCount - 3, pageCount - 2, pageCount - 1].map((pageNoi) => (
								<li className={`page-item ${pageNoi === pageIndex ? 'active' : ''}`} key={pageNoi}>
									<a
										href='#'
										className='page-link'
										onClick={(e) => {
											e.preventDefault();
											gotoPage(pageNoi);
										}}>
										{pageNoi + 1}
									</a>
								</li>
							))}

							<li className={`${canNextPage ? '' : 'disabled'} page-item`}>
								<a
									href='#'
									className='page-link'
									onClick={(e) => {
										e.preventDefault();
										nextPage();
									}}>
									<i className='fas fa-angle-right'></i>
									<span className='sr-only'>Next</span>
								</a>
							</li>
						</ul>
					</nav>
				</nav>
			</div>
		</>
	);
};

export default TableReact;
