import { useAppContext } from '../../App.Context';
import { Formik, Field, FieldArray, ErrorMessage } from 'formik';

import './Edit.Form.css';

const EditForm = () => {
	const { slectedForEdit, closeEditPopup, handleUpdate } = useAppContext();
	return (
		<Formik initialValues={slectedForEdit} onSubmit={handleUpdate}>
			{({ values, handleSubmit, isSubmitting, setFieldValue }) => (
				<form onSubmit={handleSubmit} className='edit_form'>
					<div className='form-group'>
						<label htmlFor='name'>Name</label>
						<Field type='text' className='form-control' placeholder='Name' name='name' />
						<ErrorMessage name='name' component='div' className='invalid-feedback d-block' />
					</div>

					<FieldArray name='genre'>
						{(arrayHelpers) => (
							<div className='form-group'>
								<div className='border px-4 py-2'>
									{values.genre.map((genre, index) => (
										<div key={index}>
											<div className='form-group'>
												<label htmlFor='genre'>Genre ( {index + 1} )</label>
												<div className='d-flex arry-field'>
													<div className='input-group'>
														<Field type='text' name={`genre.${index}`} className='form-control' />
													</div>
												</div>
											</div>
										</div>
									))}
								</div>
							</div>
						)}
					</FieldArray>

					{/*  */}
					<FieldArray name='developers'>
						{(arrayHelpers) => (
							<div className='form-group'>
								<div className='border px-4 py-2'>
									{values.developers.map((developers, index) => (
										<div key={index}>
											<div className='form-group'>
												<label htmlFor='developers'>Developers ( {index + 1} )</label>
												<div className='d-flex arry-field'>
													<div className='input-group'>
														<Field type='text' name={`developers.${index}`} className='form-control' />
													</div>
												</div>
											</div>
										</div>
									))}
								</div>
							</div>
						)}
					</FieldArray>

					{/*  */}
					<FieldArray name='publishers'>
						{(arrayHelpers) => (
							<div className='form-group'>
								<div className='border px-4 py-2'>
									{values.publishers.map((publishers, index) => (
										<div key={index}>
											<div className='form-group'>
												<label htmlFor='publishers'>Publishers ( {index + 1} )</label>
												<div className='d-flex arry-field'>
													<div className='input-group'>
														<Field type='text' name={`publishers.${index}`} className='form-control' />
													</div>
												</div>
											</div>
										</div>
									))}
								</div>
							</div>
						)}
					</FieldArray>

					{/*  */}
					<FieldArray name='releaseDates'>
						{(arrayHelpers) => (
							<div className='form-group'>
								<div className='border px-4 py-2'>
									{Object.keys(values.releaseDates).map((releaseDates, index) => (
										<div key={index}>
											<div className='form-group'>
												<label htmlFor='releaseDates'>Release in ( {releaseDates} )</label>
												<div className='d-flex arry-field'>
													<div className='input-group'>
														<Field
															type='text'
															name={`releaseDates.${releaseDates}`}
															className='form-control border-left'
															placeholder='Release info'
														/>
													</div>
												</div>
											</div>
										</div>
									))}
								</div>
							</div>
						)}
					</FieldArray>

					<div className='text-center d-flex justify-content-between'>
						<button className='btn btn-outline-danger mb-0' onClick={closeEditPopup}>
							Cancel
						</button>
						<button type='button' className='btn  btn-primary  mb-0 ' onClick={handleSubmit}>
							{isSubmitting ? 'Updating...' : 'Update'}
						</button>
					</div>
				</form>
			)}
		</Formik>
	);
};

export default EditForm;
