import PopupLayout from './PopUp.Layout';
import EditForm from '../Forms/Edit.Form';

const Popup = () => {
	return (
		<PopupLayout>
			<div className='card-header text-center pt-4'>
				<h5>Edit Form</h5>
			</div>
			<div className='card-body'>
				<EditForm />
			</div>
		</PopupLayout>
	);
};

export default Popup;
