import Axios from 'axios';
import React, { createContext, useContext } from 'react';
import { useState } from 'react';
import { useEffect } from 'react';

export const AppContext = createContext();

export const useAppContext = () => useContext(AppContext);

/* eslint-disable */
const Columns = Object.entries(TABLE_SCHEMA).map(([key, value]) => ({
	Header: key,
	accessor: value,
}));

const AppContextProvider = ({ children }) => {
	const [data, setData] = useState([]);
	const [isOpenPopup, setIsOpenPopup] = useState(false);
	const [slectedForEdit, setSlectedForEdit] = useState(null);

	useEffect(() => {
		/* eslint-disable */
		Axios.get(API_GET_URL).then((response) => {
			setData(response.data);
		});
	}, []);

	const openEditPopup = (id) => {
		const current = data.find((item) => item.id === id);
		setSlectedForEdit(current);
		setIsOpenPopup(true);
	};

	const closeEditPopup = () => {
		setIsOpenPopup(false);
		setSlectedForEdit(null);
	};

	const handleUpdate = async (data) => {
		await Axios.get(API_GET_URL);
		console.log(data);
	};

	return (
		<AppContext.Provider
			value={{
				data,
				Columns,
				openEditPopup,
				closeEditPopup,
				isOpenPopup,
				slectedForEdit,
				handleUpdate,
			}}>
			{children}
		</AppContext.Provider>
	);
};

export default AppContextProvider;
