import React from 'react';

import DefaultCell from './Default.Cell';
import ArrayCell from './Array.Cell';
import ReleaseDatesCell from './ReleaseDates.Cell';
// import PublishersCell from './PublishersCell';

const SCHEMA = {
	name: 'name',
	genre: 'genre',
	developers: 'developers',
	publishers: 'publishers',
	releaseDates: 'releaseDates',
};

const index = (cell) => {
	let Component;

	switch (cell.column.Header.toLowerCase()) {
		case SCHEMA.genre || SCHEMA.developers || SCHEMA.publishers:
			Component = ArrayCell;
			break;

		case SCHEMA.releaseDates:
			Component = ReleaseDatesCell;
			break;

		default:
			Component = DefaultCell;
			break;
	}

	return (
		<td {...cell.getCellProps()} className='align-middle py-2'>
			<Component cell={cell} />
		</td>
	);
};

export default index;
